import $ from "jquery";
import {
    ELEMENT_CANVAS,
    ELEMENT_DIV
} from "../Constants";
import PDF_Controller from "./PDF_Contoller";

class CanvasView {
    constructor({id}) {
        this._id = id;
        this.currentPDF = null;
    }
    getCanvas () {
        const canvas = document.createElement("canvas");
        $(canvas).attr("id", this._id, "width", "640px", "height", "480px")
            .addClass("canvas-element");
        return canvas;
    }
}

class PDF_Vew extends CanvasView {
    constructor(props = {}) {
        super({id: props.id});
        this.controller = new PDF_Controller();



    }


    render () {
        const loadButton = $("<button>load</button>").on("click", (e) => {
            this.controller.getFile("ng-book.pdf", (elem) => {
                this.currentPDF = elem;
            });
        });
        // console.log(this.controller.elemen)
        const wrapper = $(ELEMENT_DIV, {
            "class": "pdf-wrapper"
        }).append(loadButton,this.getCanvas());

        return wrapper;
    }

}

export default PDF_Vew;


