const API_GET = "GET";
const ARRAY_BUFFER = "arraybuffer";

class PDF_Model {
    constructor(props = {}) {
        this.filesUrl = props.files.length > 0 ? props.files : ["ng-book.pdf"];

        this.currentUrl = this.filesUrl[0];

        this.pageRendering = false;
        this.pageRendering = null;
        this.scale = 1;

        this.pdf = null;
        this.pageNum = 1;
    }

    loadFile(cb) {
        const self = this;
        const xhr = new XMLHttpRequest();
        xhr.open(API_GET, self.currentUrl, true);
        // xhr.responseType = 'arraybuffer';
        xhr.onload = (e) => {
            //binary form of ajax response,
            // self.prparePDF(e.currentTarget.response);
            self.loadPdf(e.currentTarget.response, cb);
        };

        xhr.onerror = function () {
            console.log("xhr error");
        };
        xhr.send();
    }

    loadPdf(data, cb) {
        const self = this;
        PDFJS.getDocument(data).then(function (pdfDoc_) {
            self.pdf = pdfDoc_;
            // document.getElementById('page_count').textContent = pdfDoc.numPages;

            // Initial/first page rendering
            self.renderPage(pageNum, cb);
        });
    }

    queueRenderPage(num, cb) {
        if (this.pageRendering) {
            this.pageNumPending = num;
        } else {
            this.renderPage(num,cb);
        }
    }

    getNext(cb) {
        if (this.pageNum <= 1) {
            return;
        }
        this.pageNum--;
        this.queueRenderPage(pageNum, cb);
    }
    getPrev (cb) {
        if (this.pageNum <= 1) {
            return;
        }
        this.pageNum--;
        this.queueRenderPage(pageNum, cb);
    }

    renderPage(num, cb) {
        const self = this;
        this.pageRendering = true;
        // Using promise to fetch the page
        this.pdf.getPage(num).then(function (page) {
            var viewport = page.getViewport(scale);
            // canvas.height = viewport.height;
            // canvas.width = viewport.width;

            // Render PDF page into canvas context
            const renderContext = {
                canvasContext: ctx,
                viewport: viewport
            };
            const element =  page.render(renderContext);
            cb(element);

            // Wait for rendering to finish
            renderTask.promise.then(function () {
                pageRendering = false;
                if (pageNumPending !== null) {
                    // New page rendering is pending
                    self.renderPage(pageNumPending);
                    pageNumPending = null;
                }
            });
        });
    }
}

export default PDF_Model;


